.Post{
     border:1px solid var(--border-color);
     background-color: var(--card-color);
     margin-top: 20px;
     border-radius: 4px;
     .heading{
          display: flex;
          align-items: center;
          gap: 10px;
          height: 60px;
          padding:  20px;
          cursor: pointer;
     }
     .content{
          height: 400px;
          img{
               width: 100%;
               height: 100%;
               object-fit: cover;
          }
     }
     .footer{
          padding: 20px;
          .like{
               display: flex;
               gap: 10px;
               cursor: pointer;
          }
          .caption{
               margin-top: 12px;
          }
          .time-ago{
               margin-top: 12px;
               color: #aaa;
          }
     }
}